import { useState, useEffect } from "react";
import { useRouter } from "next/router";
import Layout from "@/components/Layout";
import { Button, Form, Row, Col, Card } from "react-bootstrap";
import styles from "../../styles/ProductEditPage.module.css";

const ProductEditPage = () => {
  const [product, setProduct] = useState({
    name: "",
    sku: "",
    brand: "",
    price: "",
    category: "",
    stockQuantity: "",
    status: "Active",
    mainImage: null,
    sideImages: [],
    color: "",
    size: "",
    description: "",
    material: "",
  });

  const [isEditing, setIsEditing] = useState(false);
  const router = useRouter();
  const { id } = router.query;

  const categories = ["Necklaces", "Bracelets", "Earrings", "Rings", "Watches"];

  useEffect(() => {
    if (id && id !== "add") {
      // Fetch product details from API or database
      const fetchedProduct = {
        id: 1,
        name: "Gold Necklace",
        sku: "GN001",
        brand: "LuxuryBrand",
        price: "100",
        category: "Necklaces",
        stockQuantity: "50",
        status: "Active",
        mainImage: null,
        sideImages: [],
        color: "Gold",
        size: "One Size",
        description: "Elegant gold necklace.",
        material: "Gold",
      };
      setProduct(fetchedProduct);
      setIsEditing(true);
    }
  }, [id]);

  const handleFormSubmit = (e) => {
    e.preventDefault();
    console.log(isEditing ? "Updating product" : "Adding new product", product);
    router.push("/products");
  };

  const handleImageChange = (e, type) => {
    const files = Array.from(e.target.files);
    if (type === "main") {
      setProduct({ ...product, mainImage: files[0] });
    } else {
      setProduct({ ...product, sideImages: [...product.sideImages, ...files] });
    }
  };

  return (
    <Layout>
      <div className={`${styles.container} mt-5`}>
        <h1 className="text-center mb-4">{isEditing ? "Edit Product" : "Add Product"}</h1>

        <Form onSubmit={handleFormSubmit} className={styles.formContainer}>
          {/* Basic Information Section */}
          <Card className={styles.formSection}>
            <Card.Body>
              <h2 className={styles.sectionTitle}>Basic Information</h2>
              <Row>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Product Name</Form.Label>
                    <Form.Control
                      type="text"
                      value={product.name}
                      onChange={(e) => setProduct({ ...product, name: e.target.value })}
                      required
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>SKU</Form.Label>
                    <Form.Control
                      type="text"
                      value={product.sku}
                      onChange={(e) => setProduct({ ...product, sku: e.target.value })}
                      required
                    />
                  </Form.Group>
                </Col>
              </Row>
            </Card.Body>
          </Card>

          {/* Pricing Section */}
          <Card className={styles.formSection}>
            <Card.Body>
              <h2 className={styles.sectionTitle}>Pricing</h2>
              <Row>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Price ($)</Form.Label>
                    <Form.Control
                      type="number"
                      value={product.price}
                      onChange={(e) => setProduct({ ...product, price: e.target.value })}
                      required
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Stock Quantity</Form.Label>
                    <Form.Control
                      type="number"
                      value={product.stockQuantity}
                      onChange={(e) => setProduct({ ...product, stockQuantity: e.target.value })}
                      required
                    />
                  </Form.Group>
                </Col>
              </Row>
            </Card.Body>
          </Card>

          {/* Media Section */}
          <Card className={styles.formSection}>
            <Card.Body>
              <h2 className={styles.sectionTitle}>Media</h2>
              <Form.Group className="mb-3">
                <Form.Label>Main Image</Form.Label>
                <Form.Control type="file" onChange={(e) => handleImageChange(e, "main")} />
              </Form.Group>
              <Form.Group className="mb-3">
                <Form.Label>Side Images (Multiple)</Form.Label>
                <Form.Control type="file" multiple onChange={(e) => handleImageChange(e, "side")} />
              </Form.Group>
            </Card.Body>
          </Card>

          {/* Additional Details */}
          <Card className={styles.formSection}>
            <Card.Body>
              <h2 className={styles.sectionTitle}>Additional Details</h2>
              <Row>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Color</Form.Label>
                    <Form.Control
                      type="text"
                      value={product.color}
                      onChange={(e) => setProduct({ ...product, color: e.target.value })}
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Size</Form.Label>
                    <Form.Control
                      type="text"
                      value={product.size}
                      onChange={(e) => setProduct({ ...product, size: e.target.value })}
                    />
                  </Form.Group>
                </Col>
              </Row>
            </Card.Body>
          </Card>

          {/* Submit Button */}
          <Button variant="primary" type="submit" className={styles.submitButton}>
            {isEditing ? "Update Product" : "Add Product"}
          </Button>
        </Form>
      </div>
    </Layout>
  );
};

export default ProductEditPage;
